import React, { Component } from 'react';
import Research from './Research'
import './style.css';

export default class Panels extends Component {
  render(){
    return(
      <div className="container">
      <div className="card">
        <h5 className="card-header">Blast-Off</h5>
        <div className="card-body">
          <h5 className="card-title">What is Blast-Off?</h5>
          <p className="card-text">A pipeline for our research workflow.
            Hello! If you are reading this, you are most likely a part of our CRSP research group.
            This program with made for the purpose of automating a portion of our workflow and
            make it easier to annotate the staph genes. If you are not a part of our group, below is the abstract for our research project.</p>
          <a href="/research" className="btn btn-primary">See our abstract</a>
        </div>
      </div>
      </div>
    );
  }
}
